<script>
import WaveSurfer from "wavesurfer.js";
import ArrowRightIcon from "@/assets/icons/Arrow-right.vue";
export default {
  data() {
    return {
      id: "",
      wavesurfer: null,
      track:
        "https://archive.org/download/cd_hypnotize_system-of-a-down_1/disc1/04.%20System%20Of%20A%20Down%20-%20Hypnotize_sample.mp3",
      currentSeconds: 0,
      durationSeconds: null,
      trackLoaded: false,
      isGeneralComment: true,
      comment: "",
      comments: [
        {
          comment:
            "You’ve got some talented pipes, but the vocals were sometimes drown out by the sound of the cicadas.",
          timeStamp: null
        },
        {
          comment:
            "I love what you did with the snare drums here, but the transition needs work. Introduce a macarena or two.",
          timeStamp: 23
        },
        {
          comment:
            "If I could have this as my ringtone, I would. Make this the chorus and throw it in again at the 1:07 mark.",
          timeStamp: 48
        },
        {
          comment:
            "Ahhhh, the xylophone really sells it here. I say extend this part. More xylophone!",
          timeStamp: 168
        }
      ]
    };
  },
  async mounted() {
    if (!this.wavesurfer) this.createWaveSurfer();
  },
  name: "TrackFeedback",
  components: {
    ArrowRightIcon
  },
  computed: {
    waveformId() {
      return `waveform${this.id}`;
    },
    current: function() {
      if (this.trackLoaded) {
        return this.calculateDuration(this.currentSeconds);
      }
      return "0:00";
    },
    duration: function() {
      if (this.trackLoaded) {
        return this.calculateDuration(this.durationSeconds);
      }
      return "0:30";
    },
    commentTypeText() {
      if (this.isGeneralComment) {
        return "general";
      } else {
        return this.current;
      }
    },
    sortedComments() {
      return [...this.comments].sort((a, b) =>
        a.timeStamp > b.timeStamp ? 1 : -1
      );
    },
    timeStampComments() {
      return this.sortedComments.filter(x => x.timeStamp);
    },
    requiredCharCount() {
      if (this.comment.length < 120) {
        return 120 - this.comment.length;
      } else {
        return 0;
      }
    },
    disableAddCommentButton() {
      return this.requiredCharCount > 0;
    }
  },
  methods: {
    calculateDuration(seconds) {
      let sec = (seconds % 60).toFixed(0);
      const min = Math.floor(seconds / 60).toFixed(0);
      if (sec.toString().length === 1) {
        sec = "0" + sec;
      }
      return min + ":" + sec;
    },
    calculateDurationPercent(seconds) {
      const percent = (seconds / this.durationSeconds) * 100;
      return `calc( ${percent}% - 9px )`;
    },
    createWaveSurfer() {
      if (this.wavesurfer) return;
      this.id = Date.now();
      this.wavesurfer = WaveSurfer.create({
        // container: `#waveform${this.Id}`,
        container: `#waveform`,
        waveColor: "#EDEDED",
        progressColor: "#FF035A",
        cursorColor: "#3B3B3B",
        barHeight: 1,
        barMinHeight: 1,
        barWidth: 3,
        barRadius: 3,
        cursorWidth: 1,
        height: 40,
        barGap: 3
      });
      this.wavesurfer.load(this.track);

      this.wavesurfer.on("ready", () => {
        this.trackLoaded = true;
        this.durationSeconds = this.wavesurfer.getDuration();
      });

      this.wavesurfer.on("audioprocess", () => {
        this.currentSeconds = this.wavesurfer.getCurrentTime();
      });

      this.wavesurfer.on("seek", () => {
        this.isGeneralComment = false;
      });
    },
    toggleCommentType() {
      this.isGeneralComment = !this.isGeneralComment;
    },
    createCommentTypeText(timestamp) {
      if (timestamp) {
        return this.calculateDuration(timestamp);
      } else {
        return "General";
      }
    },
    addComment() {
      const comment = {
        comment: this.comment,
        timeStamp: this.currentSeconds
      };
      if (this.isGeneralComment) {
        comment.timeStamp = null;
      }
      this.comments.push(comment);
    }
  }
};
</script>

